"use client";

import { useState } from "react";
import {
  Clock,
  PlayCircle,
  Undo,
  Redo,
  Download,
  ThumbsUp,
  ThumbsDown,
  Share2,
  FileText,
  Video,
  MessageSquare,
  Settings2,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";

type Tab = {
  icon: typeof FileText;
  label: string;
  value: string;
};

const tabs: Tab[] = [
  { icon: FileText, label: "Notater", value: "notes" },
  { icon: Video, label: "Video", value: "video" },
  { icon: MessageSquare, label: "Transkripsjon", value: "transcript" },
  { icon: Settings2, label: "Innstillinger", value: "settings" },
];

export default function MeetingPage({ params }: { params: { id: string } }) {
  const [activeTab, setActiveTab] = useState("notes");

  return (
    <div className="flex h-screen">
      {/* Small Sidebar */}
      <div className="w-[68px] border-r flex flex-col items-center py-4 bg-gray-50/50">
        {tabs.map((tab) => {
          const Icon = tab.icon;
          return (
            <button
              key={tab.value}
              onClick={() => setActiveTab(tab.value)}
              className={cn(
                "w-full p-3 flex flex-col items-center gap-1 text-sm hover:bg-gray-100 transition-colors",
                activeTab === tab.value && "bg-violet-50 text-violet-600"
              )}
            >
              <Icon className="h-5 w-5" />
              <span className="text-[11px]">{tab.label}</span>
            </button>
          );
        })}
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        <div className="border-b">
          <div className="flex items-center justify-between p-4">
            <div className="flex items-center gap-4">
              <h1 className="text-xl font-semibold">
                Fireflies AI Platform Quick Overview
              </h1>
              <Button variant="outline" size="sm">
                <Clock className="h-4 w-4 mr-2" />
                Aug 08 2024
              </Button>
            </div>
            <div className="flex items-center gap-2">
              <Button variant="outline" size="sm">
                <Share2 className="h-4 w-4 mr-2" />
                Share
              </Button>
              <Button variant="outline" size="sm">
                <Download className="h-4 w-4 mr-2" />
                Download
              </Button>
            </div>
          </div>
        </div>

        <div className="flex-1 p-4 overflow-y-auto">
          <div className="max-w-4xl mx-auto space-y-4">
            <div className="flex items-start gap-4 p-4 rounded-lg border bg-white">
              <div className="w-8 h-8 rounded-full bg-violet-100 flex items-center justify-center shrink-0">
                KR
              </div>
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-1">
                  <span className="font-medium">Krish Ramineni</span>
                  <span className="text-sm text-muted-foreground">04:39</span>
                </div>
                <p className="text-sm">
                  Want to search across this meeting in an even faster way,
                  instead of just searching across the transcript...
                </p>
                <div className="flex items-center gap-2 mt-2">
                  <Button variant="ghost" size="sm">
                    <ThumbsUp className="h-4 w-4 mr-1" />
                    Helpful
                  </Button>
                  <Button variant="ghost" size="sm">
                    <ThumbsDown className="h-4 w-4 mr-1" />
                    Not Helpful
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Playback Controls */}
        <div className="border-t p-4">
          <div className="flex items-center justify-between max-w-4xl mx-auto">
            <div className="flex items-center gap-2">
              <Button variant="ghost" size="sm">
                <Undo className="h-4 w-4" />
              </Button>
              <Button variant="ghost" size="sm">
                <PlayCircle className="h-4 w-4" />
              </Button>
              <Button variant="ghost" size="sm">
                <Redo className="h-4 w-4" />
              </Button>
            </div>
            <div className="flex-1 mx-4">
              <div className="h-1 bg-gray-200 rounded-full">
                <div className="h-full w-1/3 bg-violet-600 rounded-full" />
              </div>
            </div>
            <span className="text-sm text-muted-foreground">00:11 / 08:39</span>
          </div>
        </div>
      </div>
    </div>
  );
}
